import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';

import { Card, CardContent, Typography, Button } from '@material-ui/core';
import './style.css';

import DialogAddToCart from '../../common/DialogAddToCart';

class ProductsList extends PureComponent {
  state = {
    open: false,
    selectedProduct: {}
  };

  handleOpen = product => {
    this.setState({
      open: true,
      selectedProduct: product
    });
  };

  handleClose = () => {
    this.setState({ open: false, selectedProduct: {} });
  };

  load = () => {
    this.props.fetchProducts(this.props.page, this.props.limit);
  };

  componentDidMount() {
    if (this.props.page === 1) {
      this.load();
    }
  }

  addMore = () => {
    this.load();
  };

  render() {
    const { items } = this.props.products;
    if (this.props.error)
      return (
        <div>
          <h2>Oooops... Something went wrong. Server error</h2>
          <p>{this.props.error.message}</p>
        </div>
      );
    const productsList = items.map(product => {
      return (
        <Card className="card" key={product.id}>
          <CardContent>
            <Link to={`/products/${product.id}`}>
              <img src={product.image} alt="item" />

              <Typography className="product-name" variant="h4">
                {product.name}
              </Typography>
              <Typography className="product-price" variant="h5">
                {product.price} {product.currency}
              </Typography>
            </Link>

            <Button
              variant="contained"
              color="primary"
              onClick={() => this.handleOpen(product)}
              disabled={product.amount === 0}
            >
              In Cart
            </Button>
          </CardContent>
          <DialogAddToCart
            open={this.state.open}
            handleClose={this.handleClose}
            product={this.state.selectedProduct}
          />
        </Card>
      );
    });

    return (
      <div className="product-list">
        <Button
          className="btn-show-more"
          color="primary"
          disabled={!this.props.haveMore}
          variant="contained"
          onClick={this.addMore}
        >
          ShowMore
        </Button>
        <div className="card-container">{productsList}</div>
      </div>
    );
  }
}

export default ProductsList;
