import {
  FETCH_PRODUCTS_SUCCESS,
  GRAB_PRODUCT_AMOUNT,
  ADD_PRODUCT_AMOUNT,
  ADD_PRODUCT
} from './actionTypes';

const initialState = {
  items: [],
  currentPage: 1,
  haveMore: true,
  limit: 2
};

export default function(state = initialState, action) {
  const { type } = action;
  switch (type) {
    case FETCH_PRODUCTS_SUCCESS:
      return {
        ...state,
        loading: false,
        items: [
          ...state.items,
          ...action.payload.products.filter(product =>
            state.items.every(
              nProduct => String(nProduct.id) !== String(product.id)
            )
          )
        ],
        currentPage: state.currentPage + 1,
        haveMore: state.limit === action.payload.products.length
      };
    case GRAB_PRODUCT_AMOUNT:
      return {
        ...state,
        items: state.items.map(product => {
          if (product.id === action.payload.id) {
            const newAmount = product.amount - action.payload.amount;
            return {
              ...product,
              amount: newAmount >= 0 ? newAmount : 0
            };
          }
          return product;
        })
      };
    case ADD_PRODUCT_AMOUNT:
      return {
        ...state,
        items: state.items.map(product => {
          if (product.id === action.payload.id) {
            const newAmount = product.amount + action.payload.amount;
            return {
              ...product,
              amount: newAmount >= 0 ? newAmount : 0
            };
          }
          return product;
        })
      };
    case ADD_PRODUCT:
      const isExist = state.items.every(
        product => String(action.payload.product.id) !== String(product.id)
      );

      return {
        ...state,
        items: isExist
          ? [...state.items, action.payload.product]
          : [...state.items]
      };
    default:
      return state;
  }
}
