import React, { Component } from 'react';
import {
  Paper,
  Typography,
  Button,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  TableFooter
} from '@material-ui/core';

import './style.css';

import DeleteFromCartButton from '../../common/DeleteFromCartButton';
import _ from 'lodash';
import { Link } from 'react-router-dom';

class Cart extends Component {
  render() {
    const cart = this.props.cart.map((product, index) => {
      return (
        <TableRow key={product.id * Math.random() * Date.now()}>
          <TableCell>{index + 1}</TableCell>
          <TableCell>
            <Link className="link-name" to={`/products/${product.id}`}>
              {product.name}
            </Link>
          </TableCell>
          <TableCell>{product.price}</TableCell>
          <TableCell>{product.amount}</TableCell>
          <TableCell>{product.price * product.amount}</TableCell>
          <TableCell>
            <DeleteFromCartButton product={product} />
          </TableCell>
        </TableRow>
      );
    });
    return (
      <div className="cart">
        <Paper>
          <Typography variant="h2">Your Cart</Typography>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>â„–</TableCell>
                <TableCell>Name</TableCell>
                <TableCell>Price for one</TableCell>
                <TableCell>Amount</TableCell>
                <TableCell>Cost</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>{cart}</TableBody>
            <TableFooter>
              <TableRow>
                <TableCell>Total</TableCell>
                <TableCell />
                <TableCell />
                <TableCell>
                  {_.sumBy(this.props.cart, product => product.amount)}
                </TableCell>
                <TableCell>
                  {_.sumBy(
                    this.props.cart,
                    product => product.price * product.amount
                  )}
                </TableCell>
              </TableRow>
            </TableFooter>
          </Table>
          <Button
            color="primary"
            variant="contained"
            onClick={this.props.history.goBack}
          >
            Back
          </Button>
        </Paper>
      </div>
    );
  }
}

export default Cart;
