import React, { PureComponent } from 'react';
import { Typography, Paper, Button } from '@material-ui/core';
import './style.css';
import DialogAddToCart from '../../common/DialogAddToCart';

class Product extends PureComponent {
  componentDidMount() {
    if (!this.props.product) {
      this.props.fetchProduct(this.props.match.params.id);
    }
  }

  state = {
    open: false
  };

  handleOpen = () => {
    this.setState({
      open: true
    });
  };

  handleClose = () => {
    this.setState({ open: false });
  };

  render() {
    const { product } = this.props;

    if (!product) {
      return (
        <div>
          <h2>Product with this id does not exist!</h2>
        </div>
      );
    }

    return (
      <Paper className="product">
        <div className="product-content">
          <img className="product-image-full" src={product.image} alt="item" />
          <div>
            <Typography variant="h4">{product.name}</Typography>
            <hr />
            <Typography variant="h5">
              Price: {product.price} {product.currency}
            </Typography>
            <Typography variant="h5">Left: {product.amount}</Typography>
          </div>
        </div>

        <Button
          color="primary"
          variant="contained"
          onClick={this.props.history.goBack}
        >
          Back
        </Button>
        <Button
          variant="contained"
          color="primary"
          onClick={this.handleOpen}
          disabled={product.amount === 0}
        >
          In Cart
        </Button>
        <DialogAddToCart
          open={this.state.open}
          handleClose={this.handleClose}
          product={product}
        />
      </Paper>
    );
  }
}

export default Product;
