import React, { Component } from 'react';
import {
  Typography,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField
} from '@material-ui/core';

class DialogAddToCart extends Component {
  state = {
    price: 0,
    amount: 1
  };

  componentWillReceiveProps(nextProps) {
    this.setState({ price: nextProps.product.price });
  }

  addToCart = () => {
    this.props.addToCart({
      ...this.props.product,
      amount: this.state.amount || 1
    });
    this.close();
  };

  close = () => {
    this.setState({ price: 0, amount: 1 });
    this.props.handleClose();
  };

  onChangeAmount = ({ target: { value } }) => {
    const val = Number(value);

    if (val === 0) this.setState({ amount: '' });
    else if (val <= 0) {
      this.setState(() => ({
        price: this.props.product.price,
        amount: 1
      }));
    } else if (val <= this.props.product.amount) {
      this.setState(state => ({
        price: val * this.props.product.price,
        amount: val
      }));
    } else {
      this.setState(state => ({
        price: this.props.product.amount * this.props.product.price,
        amount: this.props.product.amount
      }));
    }
  };

  render() {
    return (
      <Dialog
        open={this.props.open}
        onClose={this.props.handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {`How many ${this.props.product.name} you want?`}
        </DialogTitle>
        <DialogContent>
          <TextField
            type="number"
            label={`Amount (max ${this.props.product.amount})`}
            value={this.state.amount}
            onChange={this.onChangeAmount}
          />
          <Typography variant="p">
            Price: <span>{this.state.price}</span>
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.close} color="primary">
            Cancel
          </Button>
          <Button
            id="add-to-cart"
            onClick={this.addToCart}
            color="primary"
            autoFocus
            disabled={!this.props.open}
          >
            Add To Cart
          </Button>
        </DialogActions>
      </Dialog>
    );
  }
}

export default DialogAddToCart;
