import {
  FETCH_PRODUCTS_SUCCESS,
  ADD_PRODUCT,
  GRAB_PRODUCT_AMOUNT,
  ADD_PRODUCT_AMOUNT
} from './actionTypes';

import { makeError } from '../Error';

export function fetchProducts(page, limit) {
  return dispatch => {
    return fetch(
      `http://localhost:8000/api/products?_page=${page}&&_limit=${limit}`
    )
      .then(handleErrors)
      .then(res => res.json())
      .then(json => {
        dispatch(fetchProductsSuccess(json));
        return json;
      })
      .catch(error => dispatch(makeError(error)));
  };
}

export const fetchProduct = id => dispatch => {
  fetch(`http://localhost:8000/api/products/${id}`)
    .then(responce => {
      const status = responce.status;
      if (status >= 400) {
        throw new Error(`Product with this id does not exist ${status}`);
      }
      console.log(responce);

      return responce.json();
    })
    .then(product => {
      dispatch(addProduct(product));
    })
    .catch(error => dispatch(makeError(error)));
};

function handleErrors(response) {
  if (!response.ok) {
    throw Error(response.statusText);
  }
  return response;
}

export const fetchProductsSuccess = products => ({
  type: FETCH_PRODUCTS_SUCCESS,
  payload: { products }
});

export const grabProductAmount = (id, amount) => ({
  type: GRAB_PRODUCT_AMOUNT,
  payload: { id, amount }
});

export const addProductAmount = (id, amount) => ({
  type: ADD_PRODUCT_AMOUNT,
  payload: { id, amount }
});

export const addProduct = product => ({
  type: ADD_PRODUCT,
  payload: { product }
});
