import { connect } from 'react-redux';
import { fetchProduct } from '../../redux/ProductsList';
import { clearError } from '../../redux/Error';

const mapStateToProps = (state, props) => {
  return {
    product:
      state.products.items.filter(
        product => String(product.id) === String(props.match.params.id)
      )[0] || null,
    error: state.products.error
  };
};

const mapDispatchToProps = {
  fetchProduct,
  clearError
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
);
