import { MAKE_ERROR, CLEAR_ERROR } from './actionTypes';

const initialState = {
  error: null
};

export default function(state = initialState, action) {
  const { type } = action;
  switch (type) {
    case MAKE_ERROR:
      return {
        ...state,
        error: action.payload.error
      };

    case CLEAR_ERROR:
      return {
        ...state,
        error: null
      };
    default:
      return state;
  }
}
