import React, { Component } from "react";
import { Button } from "@material-ui/core";

class DeleteFromCartButton extends Component {
  remove = () => {
    this.props.restoreProductAmount(
      this.props.product.id,
      this.props.product.amount
    );
    this.props.deleteFromCart(this.props.product);
  };
  render() {
    return (
      <Button color="secondary" variant="contained" onClick={this.remove}>
        Remove
      </Button>
    );
  }
}

export default DeleteFromCartButton;
