import { ADD_TO_CART, DELETE_FROM_CART } from './actionTypes';
const initialState = [];

export default function(state = initialState, action) {
  const { type, payload } = action;

  switch (type) {
    case ADD_TO_CART:
      const exist =
        state.filter(prod => prod.id === action.payload.product.id)[0] || null;

      const { product } = action.payload;
      if (exist) {
        return state.map(prod => {
          if (prod.id === action.payload.product.id) {
            return {
              ...prod,
              amount: Number(prod.amount) + Number(product.amount),
              price: Number(prod.price) + Number(product.price)
            };
          }
          return prod;
        });
      } else {
        return [...state, payload.product];
      }

    case DELETE_FROM_CART:
      return state.filter(product => product !== payload.product);
    default:
      return state;
  }
}
