import { connect } from "react-redux";
import { addToCart } from "../../redux/Cart/";
import { grabProductAmount } from "../../redux/ProductsList";

const mapDispatchToProps = dispatch => {
  return {
    addToCart: product => {
      dispatch(addToCart(product));
      dispatch(grabProductAmount(product.id, product.amount));
    }
  };
};

export default connect(
  null,
  mapDispatchToProps
);
