import { connect } from "react-redux";
import { deleteFromCart } from "../../redux/Cart";
import { addProductAmount } from "../../redux/ProductsList";

const mapDispatchToProps = dispatch => ({
  deleteFromCart: (id, price) => dispatch(deleteFromCart(id, price)),
  restoreProductAmount: (id, amount) => dispatch(addProductAmount(id, amount))
});

export default connect(
  null,
  mapDispatchToProps
);
