import { fetchProducts } from '../../redux/ProductsList/';
import { connect } from 'react-redux';
import { addToCart, deleteFromCart } from '../../redux/Cart/';
import { grabProductAmount } from '../../redux/ProductsList';

const mapStateToProps = state => {
  return {
    products: state.products,
    page: state.products.currentPage,
    limit: state.products.limit,
    haveMore: state.products.haveMore,
    error: state.error.error
  };
};

const mapDispatchToProps = dispatch => {
  return {
    fetchProducts: (page, limit) => dispatch(fetchProducts(page, limit)),
    addToCart: product => {
      dispatch(addToCart(product));
      dispatch(grabProductAmount(product.id, product.amount));
    },
    deleteFromCart: id => dispatch(deleteFromCart(id))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
);
